.header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 56px;
	background: #388E3C;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
	z-index: 50;
}

.container {
	display: flex;
}

.container .brand {
	margin: 0;
	font-size: 24px;
	line-height: 56px;
	font-weight: 400;
	color: #FFF;
}

.container .spacer {
	flex: 1;
}

.container .links {
	display: none
}

.container .links .link svg {
	margin: 16px 8px 16px 8px;
}

.container .links .link:first-child svg {
	margin-left: 0;
}

.container .links .link:last-child svg {
	margin-right: 0;
}

.container .links svg path {
	fill: #FFF;
}

@media (min-width: 410px) {
  .container .links {
    display: block;
  }
}


