.card {
  background-color: #ffffff;
  border-radius: 3px;
  box-shadow: 0 1px 1px 0 rgba(60,64,67,.08), 0 1px 3px 1px rgba(60,64,67,.16);
  padding: 16px;
}

.cardHeader {
  margin: 0;
  margin-bottom: 8px;
}

@media (max-width: 575.98px) {
  .card {
    margin-left: -15px;
    margin-right: -15px;
  }
}


